.people {
  padding: var(--main-pad);
  max-width: var(--main-w-main);
  flex-flow: wrap;
  gap: 40px 30px;
  width: 100%;
  margin: 0 auto;
  display: flex;
}

.people .person {
  flex-direction: column;
  align-items: center;
  max-width: 190px;
  transition: all 1s;
  display: flex;
}

.people .person .extra {
  text-align: center;
  visibility: hidden;
  padding-top: 10px;
}

.people .person .name {
  letter-spacing: 0;
  text-align: center;
  color: var(--second-color);
  font-size: 20px;
  font-weight: 700;
  line-height: 100%;
}

.people .person .photo {
  position: relative;
}

.people .person .photo img {
  border-radius: 50%;
  width: 100%;
  max-width: 100%;
  height: 200px;
  transition: all 1s;
}

.people .person .photo .rank {
  background: var(--main-color);
  text-align: center;
  width: 32px;
  height: 32px;
  vertical-align: center;
  letter-spacing: 0;
  color: var(--rank-color);
  border-radius: 50%;
  margin-right: -5%;
  font-size: 16px;
  font-weight: 700;
  line-height: 32px;
  position: absolute;
  top: 50%;
  right: 1%;
  transform: translate(-50%, -10%);
}

* {
  box-sizing: border-box;
}

:root {
  --main-pad: 30px 35px;
  --main-color: #fff;
  --rank-color: #8b0000;
  --second-color: #2f4f4f;
  --main-w-main: 484px;
  --w-person: 190px;
}

@media (width >= 1000px) {
  :root {
    --main-pad: 30px 55px;
    --main-w-main: 100%;
    --w-person: 190px;
  }

  .people .person {
    flex: 190px;
    max-width: 200px;
  }
}

@media (width >= 1500px) {
  .people .person {
    cursor: pointer;
    flex: 190px;
    max-width: 200px;
  }

  .people .person:hover {
    transform: scale(1.1);
  }

  .people .person:hover .photo img {
    border-radius: 60px;
  }

  .people .person:hover .extra {
    visibility: visible;
  }

  :root {
    --main-pad: 30px 55px;
    --main-w-main: 1000px;
    --w-person: 190px;
  }
}
